      <el-breadcrumb-item>{{ otherDeliveryAddGet.other_delivery_id ? "修改" : "新增" }}其他发货
      </el-breadcrumb-item>
    </el-breadcrumb>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="paramsRef">
          <el-form-item label="单据号" size="mini" v-if="otherDeliveryAddGet.other_delivery_id">
            <el-input v-model="params.other_delivery_sn" placeholder="请输入" disabled></el-input>
          </el-form-item>
          <el-form-item label="销售组织" size="mini" :rules="rules.company_id" prop="company_id" @focus="companyListInvok">
            <el-select v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}
                </span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            {{(function(){if(params.department_id == 0 )params.department_id = null})()}}
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok" @change="departmentChange">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="单据号" size="mini" v-if="salesdelieveryAddGet.outstock_id">
            {{params.delivery_sn}}
          </el-form-item> -->
          <el-form-item label="业务日期" size="mini" :rules="rules.business_time" prop="business_time">
            <el-date-picker :clearable="false" :editable="false" v-model="params.business_time" type="date" :default-value="params.business_time" value-format="yyyy-MM-dd" placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="发货方式" size="mini">
            {{(function(){if(params.send_mode==0)params.send_mode=null})()}}
            <el-select clearable v-model="params.send_mode" filterable :disabled="params.send_mode==3?true:false">
              <el-option label="物流发货" :value="1">物流发货</el-option>
              <el-option label="客户自提" :value="2">客户自提</el-option>
              <el-option label="自提物流" :value="3">自提物流</el-option>
            </el-select>
          </el-form-item>
          <!-- 物流公司绑定name 防止出现数字的bug -->
          <el-form-item label="物流公司" size="mini">
            {{(function(){if(params.logistics_id==0)params.logistics_id=null})()}}
            <el-select clearable v-model="params.logistics_id" @focus="logisticsInvok" filterable @change="logisticsChange">
              <el-option v-for="item in logisticsList" :key="item.logistics_id" :label="item.logistics_name" :value="item.logistics_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="物流票号" size="mini">
            <el-input v-model="params.logistics_number" @change="logistics_numberChange" @click.native="logistics_numberFormClick" :disabled="logistics_numberFormDisabled" placeholder="请输入">
            </el-input>
          </el-form-item>

          <el-form-item label="运费金额" size="mini">
            <el-input v-model="params.postage_price" placeholder="请输入" @change="handleEdit"></el-input>
          </el-form-item>

          <el-form-item label="运费结算方式" size="mini">
            {{(function(){if(params.postage_type_id == 0 )params.postage_type_id = null})()}}
            <el-select clearable v-model="params.postage_type_id" @click.native="postageTypeFormClick" @focus="PostageTypeListInvok" @change="postageTypeFormChange" :disabled="postageTYPEform">
              <el-option v-for="item in postageTypeList" :key="item.postage_type_id" :label="item.postage_type_name" :value="item.postage_type_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="发货类型" size="mini" :rules="rules.delivery_type" prop="delivery_type">
            <el-select v-model="params.delivery_type" @change="delivery_type_change" :disabled="otherDeliveryAddGet.other_delivery_id?true:false" placeholder="请选择">
              <el-option label="三包出库发货" :value="1">三包出库发货</el-option>
              <el-option label="返胎出库发货" :value="2">返胎出库发货</el-option>
              <el-option label="旧胎退回发货" :value="3">旧胎退回发货</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="司机" size="mini">
            {{(function(){if(params.logistics_driver_id==0)params.logistics_driver_id=null})()}}
            <el-select clearable v-model="params.logistics_driver_id" placeholder="请选择" filterable @change="logistics_driverChange" @focus="LogisticsDriverInvok">
              <ul class="customer">
                <li style="margin:0 15px;font-size:14px;">司机名称</li>
                <li style="float:right;margin:0 15px;font-size:14px;">司机电话</li>
              </ul>
              <el-option v-for="item in logistics_driveList" :key="item.logistics_driver_id" :label="item.logistics_driver_name" :value="item.logistics_driver_id">
                <ul class="customer">
                  <li style="margin:0 15px" :title="item.logistics_driver_name">{{ item.logistics_driver_name }}</li>
                  <li style="float:right" :title="item.phone">{{item.phone}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="车牌号" size="mini">
            <el-input type="text" v-model="params.license_number" maxlength="300" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="备注" size="mini">
            <el-input type="text" v-model="params.remark" maxlength="300" placeholder="请输入"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-show="params.send_mode==3?false:true">引用单据</el-button>
        <el-button plain icon="el-icon-delete" @click="delF" class="addBtn" size="mini" v-show="params.send_mode==3?false:true">删行</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @selection-change="handleSelectionChange" max-height="600" show-summary :summary-method="getSummaries">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="序号" type="index" width="46" align="center"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织" align="center"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" width="150" label="部门" align="center"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_sn" width="156" label="出库单号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" width="120" label="收货单位">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_mobile" width="120" label="手机号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" width="80" label="商品编号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" width="400" label="商品名称">
            <template slot-scope="scope">
              <pre>{{ scope.row.goods_name }}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="batch" width="88" label="年周/批次">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="delivery_goods_num" width="110" label="发货数量" align="right">
            <template slot-scope="scope">
              <el-input size="small" :disabled="params.send_mode==3?true:false" class="editor" v-model="scope.row.delivery_goods_num" @change="goods_num_change(scope.row)" suffix-icon="el-icon-edit-outline" placeholder="请输入"></el-input>
            </template>
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" width="150" label="物流公司">
            <template slot-scope="scope">
              <el-select clearable v-model="scope.row.logistics_id" @focus="event => tableDownLogistics('', scope.row)" @change="(value)=>{logisticsChangeDate(value,scope.row)} ">
                <el-option v-for="item in scope.row.logisticsList" :key="item.logistics_id" :label="item.logistics_name" :value="item.logistics_id">
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="logistics_number" width="110" label="物流票号" align="right">
            <template slot-scope="scope">
              <el-input size="small" class="editor" v-model="scope.row.logistics_number" @click.native="logistics_numberListClick" @change="logistics_numberList" :disabled="params.logistics_number?true:false" suffix-icon="el-icon-edit-outline" placeholder="请输入"></el-input>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="postage_price" width="110" label="运费金额" align="right">
            <template slot-scope="scope">
              <el-input size="small" class="editor" v-model="scope.row.postage_price" @change="postage_ListChange(scope.row)" @click.native="postage_ListClick" :disabled="postage_priceDisList" suffix-icon="el-icon-edit-outline" placeholder="请输入"></el-input>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="postage_type_id" width="110" label="运费结算方式" align="right">
            <template slot-scope="scope">
              {{(function(){if(scope.row.postage_type_id==0)scope.row.postage_type_id=null})()}}
              <el-select clearable v-model="scope.row.postage_type_id" @change="postageTypeChange(scope.row)" @focus="PostageTypeListInvok" @click.native="postageTypeListClick" :disabled="params.postage_type_id?true:false">
                <el-option v-for="item in postageTypeList" :key="item.postage_type_id" :label="item.postage_type_name" :value="item.postage_type_id">
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
            <template slot-scope="scope">
              <el-input size="small" class="editor" v-model="scope.row.remark" suffix-icon="el-icon-edit-outline" placeholder="请输入"></el-input>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" width="140" label="区域">
            <template slot-scope="scope">
              <span>{{scope.row.province_name+scope.row.city_name+scope.row.area_name}}</span>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="address" width="140" label="详细地址">
          </el-table-column>
        </el-table>
      </div>
      <div v-if="dialogVisible">
        <el-dialog :close-on-click-modal="false" :title="title" class="dialogForm" :visible.sync="dialogVisible" width="1100px" @close="closeDialog">
          <Document @dialogformInline="getOutstockData" @closedialog="closeDialog" :dialogParams="params"></Document>
        </el-dialog>
      </div>
      <div class="dialog-footer" style="text-align:center;margin-top:20px;">
        <el-button @click="goBack">取 消</el-button>
        <el-button type="primary" @click="submit('paramsRef')">确 定</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import Document from "./outstock_dialog.vue";
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "instorageOtherDeliveryOtherDeliveryAdd",
  data() {
    return {
      tableDownLoading: false, // table 下拉加载
      authority: {},
      loading:false,
      lock: 0,
      outerVisible: false,
      dialogVisible: false,
      pages: 1,
      TableData: {
        tableData: [],
        total: 10
      },
      title: "",
      timeValue: [],
      options: {
        options: []
      },
      params: {
        company_id: null,
        department_id: null
      },
      multipleSelection: [], //已选项
      postageTYPEtable: false, //table 结算方式
      postageTYPEform: false, //form 结算方式
      postageTYPE: false,
      logistics_numberDisabled: false,
      logistics_numberFormDisabled: false,
      relevancy_sn: "relevancy_sn",
      postagePriceDisForm: false,
      postage_priceDisList: false,
      flag: false
    };
  },
  components: {
    Document
  },
  computed: {
    ...mapGetters(["otherDeliveryAddGet"])
  },
  methods: {
    ...mapActions([
      "otherDeliveryAddACT",
      "otherDeliveryIndexACT",
      "otherDeliverySeeACT"
    ]),
    // 接口请求物流公司（解决表头表体共用同一方法出现闪烁id问题）
    tableDownLogistics(parameter, row) {
      this.tableDownLoading = true; // 开启加载
      //物流公司
      let params = {
        company_id: this.params.company_id ? this.params.company_id : "",
        logistics_name: parameter
      };
      this.$set(row, "logisticsList", []);
      this.$axMethod
        .getLogisticsList(params)
        .then(({ data }) => {
          this.tableDownLoading = false; // 关闭加载
          if (data.code == 200) {
            let result = data.data;
            this.$set(row, "logisticsList", result);
          }
        })
        .catch(error => {
          this.tableDownLoading = false; // 关闭加载
        });
    },
    logisticsChangeDate(val, row) {
      if (!val) {
        row.logistics_id = "";
        this.$set(this.params, "logistics_id", '');
        this.$set(this.params, "logistics_name", '');
        return;
      }
      var box = row.logisticsList.filter((item) => item.logistics_id === val);
      row.logistics_name = box[0] ? box[0].logistics_name : box[0]
      var step = this.TableData.tableData.every((item) => item.logistics_id === val);
      if (step) {
        this.$set(this.params, "logistics_id", row.logistics_id);
        this.$set(this.params, "logistics_name", row.logistics_name);
        this.logisticsList.push({
          logistics_id: row.logistics_id,
          logistics_name: row.logistics_name
        });
      } else {
        this.$set(this.params, "logistics_id", '');
        this.$set(this.params, "logistics_name", '');
      }
    },
    postage_FormClick() {
      if (this.postagePriceDisForm) {
        this.$message({
          type: "warning",
          message: "清除表格所有运费金额才可编辑！"
        });
      }
    },
    postage_ListClick() {
      if (this.postage_priceDisList) {
        this.$message({
          type: "warning",
          message: "清除表头运费金额才可编辑！"
        });
      }
    },
    logistics_numberListClick() {
      //表格物流票号
      if (this.logistics_numberDisabled) {
        this.$message({
          type: "warning",
          message: "清除表头物流票号才可编辑！"
        });
      }
    },
    logistics_numberFormClick() {
      //表头物流票号
      if (this.logistics_numberFormDisabled) {
        this.$message({
          type: "warning",
          message: "清除表格所有物流票号才可编辑！"
        });
      }
    },
    postageTypeListClick() {
      //表格运费结算方式
      if (this.postageTYPEtable) {
        this.$message({
          type: "warning",
          message: "清除表头运费结算方式才可选择！"
        });
      }
    },
    postageTypeFormClick() {
      //表头运费结算方式
      if (this.postageTYPEform) {
        this.$message({
          type: "warning",
          message: "清除表格所有运费结算方式才可选择！"
        });
      }
    },
    logistics_numberList(val) {
      this.logistics_numberFormDisabled = true;
      if (!val) {
        this.TableData.tableData.forEach(item => {
          if (!item.logistics_number) {
            this.logistics_numberFormDisabled = false;
          } else {
            this.logistics_numberFormDisabled = true;
          }
        });
      }
    },
    logistics_numberChange(val) {
      if (val) {
        this.TableData.tableData.forEach(item => {
          this.$set(item, "logistics_number", this.params.logistics_number);
        });
        this.logistics_numberDisabled = true;
      } else {
        this.logistics_numberDisabled = false;
      }
    },
    postageTypeFormChange(val) {
      if (val) {
        this.params.postage_type_name = this.postageTypeList.filter(
          obj => obj.postage_type_id === val
        )[0].postage_type_name;
        this.postageTYPEtable = true;
        this.TableData.tableData.forEach(item => {
          this.$set(item, "postage_type_id", this.params.postage_type_id);
          this.$set(item, "postage_type_name", this.params.postage_type_name);
        });
      } else {
        this.postageTYPEtable = false;
        this.postageTYPEform = true;
      }
    },
    postageTypeChange(row) {
      this.postageTYPEform = true;
      this.$set(this.params, "postage_type_name", "");
      this.$set(this.params, "postage_type_id", "");
      if (!row.postage_type_id) {
        this.TableData.tableData.forEach(item => {
          if (!item.postage_type_id) {
            this.$set(item, "postage_type_name", "");
            this.$set(item, "postage_type_id", "");
            this.postageTYPEform = false;
          } else {
            this.postageTYPEform = true;
          }
        });
      } else {
        let Changepostage_type_name = this.postageTypeList.filter(
          obj => obj.postage_type_id === row.postage_type_id
        )[0].postage_type_name;
        this.$set(row, "postage_type_name", Changepostage_type_name);
      }
    },
    delivery_type_change(val) {
      this.$set(this.TableData, "tableData", []);
    },
    logistics_driverChange(val) {
      if (val) {
        this.$set(
          this.params,
          "license_number",
          this.logistics_driveList.filter(
            obj => obj.logistics_driver_id === val
          )[0].license_number
        );
      }
    },
    departmentChange() {
      this.$set(this.TableData, "tableData", []);
    },
    logisticsChange(val) {
      this.params.logistics_name =
        this.logisticsList.filter(obj => obj.logistics_id === val)[0]
          .logistics_name || 0;
      if (this.TableData.tableData.length > 0) {
        this.TableData.tableData.forEach(item => {
          item.logistics_id = val;
          item.logistics_name = this.params.logistics_name;
          this.$set(item, "logisticsList", [
            {
              logistics_id: val,
              logistics_name: this.params.logistics_name
            }
          ]);
        });
      }
    },
    companyChange(val) {
      this.$set(this.params, "logistics_id", 0);
      this.$set(this.params, "company_id", val);
      this.$set(this.params, "department_id", 0);

      this.params.company_name =
        this.companyList.filter(obj => obj.company_id === val)[0]
          .company_name || 0;
      this.TableData.tableData.forEach(item => {
        item.logistics_id = 0;
        item.logistics_name = "";
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    goBack() {
      this.$router.push({
        path: "/inventory/otherdelivery"
      });
    },
    getOutstockData(val) {

      this.dialogVisible = false;
      var LogisticsLable = 'intentional_logistics', LogisticsValue = 'intentional_logistics_id';// 物流公司的key value
      if (this.params.delivery_type === 1) {
        // 三包出库
        LogisticsLable = 'settlement_customer_name'
        LogisticsValue = 'settlement_customer_id'
      }
      let outstockIdArr = this.TableData.tableData.map(item => {
        return item.outstock_id;
      });
      let newVal = val.map(obj => {
        return Object.assign({}, obj, {
          delivery_goods_num: obj.no_delivery_num,
          logistics_id: obj[LogisticsValue] == 0 ? null : obj[LogisticsValue] ,
          logistics_name: obj[LogisticsLable],
          logisticsList: [
            {
              logistics_id: obj[LogisticsValue],
              logistics_name: obj[LogisticsLable]
            }
          ]
        });
      });
      newVal.forEach(item => {
        if (!outstockIdArr.includes(item.outstock_id)) {
          this.TableData.tableData.push(item);
        }
      });
      if (this.params.logistics_number != null) {
        this.TableData.tableData.forEach(item => {
          this.$set(item, "logistics_number", this.params.logistics_number);
        });
      }
      if (this.params.postage_type_id != null) {
        this.TableData.tableData.forEach(item => {
          this.$set(item, "postage_type_id", this.params.postage_type_id);
          this.$set(item, "postage_type_name", this.params.postage_type_name);
        });
        this.postageTYPEtable = true;
      }
      var row = this.TableData.tableData[0];
      row && this.logisticsChangeDate(row.logistics_id, row);
      if (this.params.postage_price != null) {
        this.handleEdit(this.params.postage_price);
        this.postage_priceDisList = true;
      }
    },
    closeDialog() {
      this.dialogVisible = false;
    },
    addF() {
      if (!this.params.delivery_type) {
        this.$message.warning("请先选择发货类型");
        return false;
      }
      switch (this.params.delivery_type) {
        case 1:
          this.title = "引用三包出库单列表";
          break;
        case 2:
          this.title = "引用返胎出库单列表";
          break;
        case 3:
          this.title = "引用旧胎退回单列表";
          break;
        default:
          break;
      }
      this.dialogVisible = true;
    },
    delF() {
      //删行
      if (this.multipleSelection.length) {
        let a = new Set(this.TableData.tableData);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.$set(this.TableData, "tableData", Array.from(differenceABSet));
      } else {
        this.$message({
          message: "请先选择要删除的数据！",
          type: "warning"
        });
      }
    },
    submit(paramsRef) {
      if (this.TableData.tableData.length < 1) {
        this.$message({
          type: "warning",
          message: "请先引用单据"
        });
        return false;
      }
      let obj = [];
      this.TableData.tableData.forEach((item, index) => {
        if (!item.delivery_goods_num) {
          this.$message.warning("请输入发货数量");
          this.flag = true;
          return false;
        } else {
          this.flag = false;
        }
      });
      if (this.flag) {
        return false;
      }
      this.TableData.tableData.forEach((item, index) => {
        // return false;
        obj.push({
          relevancy_id: item.relevancy_id
            ? item.relevancy_id
            : item.outstock_id,
          relevancy_sn: item.relevancy_sn
            ? item.relevancy_sn
            : item.outstock_sn,
          relevancy_goods_id: item.relevancy_goods_id
            ? item.relevancy_goods_id
            : item.outstock_goods_id,
          company_name: item.company_name,
          company_id: item.company_id,
          department_id: item.department_id,
          department_name: item.department_name,
          customer_id: item.customer_id,
          customer_name: item.customer_name,
          goods_id: item.goods_id,
          customer_mobile: item.customer_mobile,
          goods_sn: item.goods_sn,
          goods_name: item.goods_name,
          batch_id: item.batch_id,
          batch: item.batch,
          tire_un: item.tire_un,
          intentional_id: item.intentional_id ? item.intentional_id : "",
          warehouse_id: item.warehouse_id,
          warehouse_name: item.warehouse_name,
          allocation_id: item.allocation_id,
          allocation_name: item.allocation_name,
          tire_un: item.tire_un,
          goods_num: item.goods_num,
          delivery_goods_num: item.delivery_goods_num,
          logistics_number: item.logistics_number ? item.logistics_number : "",
          postage_price: item.postage_price ? item.postage_price : "",
          postage_type_id: item.postage_type_id ? item.postage_type_id : 0,
          postage_type_name: item.postage_type_name
            ? item.postage_type_name
            : "",
          intentional_logistics: item.intentional_logistics,
          logistics_id: item.logistics_id || 0,
          logistics_name: item.logistics_name || "",
          remark: item.remark ? item.remark : "",
          address: item.address || "",
          province_name: item.province_name || "",
          province_id: item.province_id || "",
          city_name: item.city_name || "",
          city_id: item.city_id || "",
          area_name: item.area_name || "",
          area_id: item.area_id || ""
        });
      });

      this.params.postage_price = this.params.price;

      this.$refs[paramsRef].validate(valid => {
        let datas = {
          ...this.params,
          delivery_info: JSON.stringify(obj)
        };
        if (!this.otherDeliveryAddGet.other_delivery_id) {
          this.$axios({
            url: "v2/inventory/OtherDelivery/addOtherDelivery",
            method: "post",
            data: datas
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                type: "success",
                message: res.data.msg
              });
              this.goBack();
            }
          });
        } else {
          this.$axios({
            url: "v2/inventory/OtherDelivery/editOtherDelivery",
            method: "post",
            data: datas
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                type: "success",
                message: res.data.msg
              });
              this.goBack();
            }
          });
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        if (column.property === "delivery_goods_num") {
          const values = data.map(item => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index]);
          this.params.delivery_num = sums[index];
        }
        if (column.property === "postage_price") {
          const values = data.map(item => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index]).toFixed(2);
          this.params.price = sums[index];
        }
      });
      return sums;
    },
    postage_ListChange(row) {
      if (row.postage_price < 0 || isNaN(row.postage_price)) {
        this.$message.warning("只能输入正数");
        this.$set(row, "postage_price", 0);
      }
      this.$set(this.params, "postage_price", "");
      if (row.postage_price) {
        this.postagePriceDisForm = true;
      } else {
        this.TableData.tableData.forEach(item => {
          if (!item.postage_price) {
            this.postagePriceDisForm = false;
          } else {
            this.postagePriceDisForm = true;
          }
        });
      }
    },
    handleEdit(val) {
      if (!val) {
        this.postage_priceDisList = false;
        return false;
      }
      if (val < 0 || isNaN(val) || val == 0) {
        //this.$message.warning("只能输入正数");
        this.$set(this.params, "postage_price", "");
        this.flag = true;
        return false;
      } else {
        this.flag = false;
      }
      if (this.TableData.tableData < 1) return false;
      let obj = [];
      let nums = false;
      this.TableData.tableData.forEach(item => {
        if (
          item.delivery_goods_num < 0 ||
          item.delivery_goods_num == 0 ||
          isNaN(item.delivery_goods_num)
        ) {
          nums = true;
          this.$message.warning("请输入发货数量");
          return false;
        } else {
          nums = false;
        }
        if (nums) {
          this.$message.warning("请输入发货数量");
          // this.flag = true;
          return false;
        } else {
          this.flag = false;
        }
        obj.push({
          relevancy_id: item.relevancy_id
            ? item.relevancy_id
            : item.outstock_id,
          relevancy_sn: item.relevancy_sn
            ? item.relevancy_sn
            : item.outstock_sn,
          relevancy_goods_id: item.relevancy_goods_id
            ? item.relevancy_goods_id
            : item.outstock_goods_id,
          customer_id: item.customer_id,
          customer_name: item.customer_name,
          customer_mobile: item.customer_mobile,
          goods_id: item.goods_id,
          goods_sn: item.goods_sn,
          goods_name: item.goods_name,
          batch_id: item.batch_id,
          batch: item.batch,
          tire_un: item.tire_un,
          intentional_id: item.intentional_id ? item.intentional_id : "",
          warehouse_id: item.warehouse_id,
          warehouse_name: item.warehouse_name,
          allocation_id: item.allocation_id,
          allocation_name: item.allocation_name,
          tire_un: item.tire_un,
          goods_num: item.goods_num,
          delivery_goods_num: item.delivery_goods_num,
          logistics_number: item.logistics_number ? item.logistics_number : "",
          postage_price: item.postage_price ? item.postage_price : "",
          postage_type_id: item.postage_type_id ? item.postage_type_id : 0,
          postage_type_name: item.postage_type_name
            ? item.postage_type_name
            : "",
          intentional_logistics: item.intentional_logistics,
          logistics_id: item.logistics_id || 0,
          logistics_name: item.logistics_name || "",
          remark: item.remark ? item.remark : ""
        });
      });
      this.$axios({
        url: "open/CountApportion/getCountOtherDeliveryApportion",
        method: "post",
        data: {
          postage_price: val,
          delivery_info: JSON.stringify(obj)
        }
      }).then(res => {
        if (res.data.code == 200) {
          for (var i = 0; i < this.TableData.tableData.length; i++) {
            this.$set(
              this.TableData.tableData[i],
              "postage_price",
              res.data.data.goods_list[i].postage_price
            );
          }
        }
      });
      this.postage_priceDisList = true;
    },
    goods_num_change(row) {
      if (
        row.delivery_goods_num < 0 ||
        isNaN(row.delivery_goods_num) ||
        row.delivery_goods_num == 0
      ) {
        // row.delivery_goods_num = 1;
        this.$set(row, "delivery_goods_num", 0);
        this.$message.warning("只能输入正数");
      } else {
        this.handleEdit(this.params.postage_price);
      }
    }
  },
  mounted() {
    if (this.otherDeliveryAddGet.other_delivery_id) {
      // this.logisticsInvok(); //物流公司
      // this.PostageTypeListInvok();
      this.postagePriceDisForm = true;
      this.postage_priceDisList = false;
      this.relevancy_sn = "relevancy_sn";
      this.$axMethod
        .getOtherDeliveryInfo({
          other_delivery_id: this.otherDeliveryAddGet.other_delivery_id
        })
        .then(res => {
          if (res.data.code == 200) {
            Object.assign(res.data.data, {
              logistics_driver_id: res.data.data.driver_id,
              logistics_driver_name: res.data.data.driver_name
            });
            // 因进入修改页面后会显示物流公司的Id故需要请求一遍物流公司下拉id
            res.data.data.goods_info.forEach(item => {
              this.$set(item, "logisticsList", [
                {
                  logistics_id: item.logistics_id,
                  logistics_name: item.logistics_name
                }
              ]);
            });
            this.$set(this, "params", res.data.data);
            if (this.params.logistics_number) {
              this.logistics_numberDisabled = true;
            }
            if (this.params.postage_price) {
              this.postage_priceDisList = false;
            }

            if (this.params.postage_type_id) {
              this.postageTYPEtable = true;
            }

            this.TableData.tableData = res.data.data.goods_info;
            opthionList(this, res.data.data);
            res.data.data.goods_info.forEach(val => {
              this.postageTypeList.push({
                postage_type_id: val.postage_type_id,
                postage_type_name: val.postage_type_name
              });
            });
          }
        });
    } else {
      this.relevancy_sn = "outstock_sn";
      let { employee_id, company_admin_id } = localStorage;
      if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
        this.$set(this, "params", {
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id),
          logistics_id: 0,
          logistics_number: "",
          postage_price: null,
          postage_type_id: 0,
          logistics_driver_id: 0,
          license_number: ""
        });
      } else {
        this.$set(this, "params", {
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id),
          department_id: Number(localStorage.department_id),
          delivery_type: 1,
          logistics_id: 0,
          logistics_number: "",
          postage_price: null,
          postage_type_id: 0,
          logistics_driver_id: 0,
          license_number: ""
        });
      }
      opthionList(this, window.localStorage);
    }
    // this.PostageTypeListInvok(); //结算方式
    // this.LogisticsDriverInvok(); //司机
  }
};
</script>

