                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 吕涛 说先 注释掉 -->
          <!-- <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" filterable placeholder="请选择">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList.options"></el-option>
            </el-select>
          </el-form-item> -->
          <!-- <el-form-item :label="order_snTitle" size="mini" label-width="130px">
            <el-input v-model="params.reborn_outstock_sn" v-if="this.dialogParams.delivery_type==2" class="shortSearchInput" placeholder="请输入"></el-input>
            <el-input v-model="params.outstock_sn" v-else class="shortSearchInput" placeholder="请输入"></el-input>
          </el-form-item> -->
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @selection-change="handleCurrentChanges" max-height="240">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" width="150" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_sn" label="单据号" min-width="160">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" min-width="100">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" :label="customer_nameTitle" min-width="150">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="no_delivery_num" label="未发货数量" min-width="80" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="delivery_num" label="已发货数量" min-width="80" align="right">
          </el-table-column>
          <!-- <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="仓库名称" width="132">
        </el-table-column> -->
          <el-table-column :show-overflow-tooltip="true" v-if="dialogParams.delivery_type==1" property="order_sn" label="关联单据号" min-width="170">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="settlement_customer_name" width="130" label="物流公司">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="abstract" label="备注" min-width="110">
          </el-table-column>
        </el-table>
      </div>

      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>

      <div style="padding-top: 20px;border-top: 1px dotted #333;margin-top: 20px;">
        <p>单据明细</p><br />
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableDataInfo" ref="TableDataInfoRef" style="width: 100%" @selection-change="handleSelectionChange" max-height="240">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" @selection-change="handleSelectionChange">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" type="index" width="50" label="序号"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" width="150" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_sn" label="单据号" min-width="170">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" min-width="170">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
            <template slot-scope="scope">
              <pre>{{scope.row.goods_name}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次"> </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="tire_un" label="胎号" v-if="dialogParams.delivery_type!=1"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="no_delivery_num" label="未发货数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="仓库名称" width="132">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_appraisal_sn" v-if="dialogParams.delivery_type==3" label="关联单据号" width="170">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="order_sn" v-if="dialogParams.delivery_type==2" label="关联单据号" width="170">
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogCancelForm()">取 消</el-button>
        <el-button type="primary" @click="dialogSubmitForm()">确 定</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getData,
  getCustomerList,
  getCompanyListCommon
} from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      radio: null,
      timeValue: "", //选择日期
      params: {},
      companyList: [],
      dialogSupplierList: [], // 弹框供应商
      loading: false,
      customerList:  [],
      searchParams: {},
      multipleSelection: [],
      TableData: {
        tableData: [],
        total: 0
      },
      TableDataInfo: [], //详情数组
      warehouseList: {
        options: []
      }, //仓库管理
      pageSize: 30,
      goPage: 1,
      per_page: [30, 50, 100, 500],
      customer_nameTitle: "",
      order_snTitle: "",
      Api: "",
      type: 0
    };
  },
  props: ["dialogParams"],
  methods: {
    companyFocus(val) {
      getCompanyListCommon(this);
    },
    selectedTable() {
      this.$refs.TableDataInfoRef.toggleAllSelection();
    },
    handleCurrentChanges(val) {
      if (val.length === 0) {
        this.TableDataInfo = [];
        return;
      }
      if (this.type == 3) {
        this.$axios({
          method: "get",
          url: "Publics/OtherDelivery/referenceRebornOutstockInfo",
          params: {
            reborn_outstock_id: val.map(obj => obj.outstock_id).join(),
            warehouse_id: this.params.warehouse_id
          }
        }).then(res => {
          if (res.data.code == 200) {
            this.TableDataInfo = res.data.data;
            this.selectedTable();
          }
        });
      } else {
        this.$axios({
          method: "get",
          url: this.$api.referenceOutstockInfoQTFH,
          params: {
            outstock_id: val.map(obj => obj.outstock_id).join(),
            warehouse_id: this.params.warehouse_id
          }
        }).then(res => {
          if (res.data.code == 200) {
            this.TableDataInfo = res.data.data;
            this.selectedTable();
          }
        });
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    getCustomerListFocus() {
      // this.$common.options("getSupplierListOption", this.customerList, {});
      this.loading = true;
      this.$axMethod
        .getSupplierListOpen({ company_id: this.params.company_id })
        .then(({ data }) => {
          if (data.code == 200) {
            this.loading = false;
            let result = data.data;
            vm.$set(vm, "dialogSupplierList", result);
          }
        })
        .catch(error => {
          this.loading = false;
        });
    },
    dialogCancelForm() {
      this.$emit("closedialog", false);
    },
    dialogSubmitForm() {
      if (this.multipleSelection.length) {
        this.$emit("dialogformInline", this.multipleSelection);
      } else {
        this.$message.error("请选择一项");
      }
    },
    handleSizeChange(val) {
      Object.assign(this.params, {
        per_page: val,
        page: 1
      });
      this.getThisData();
    },
    handleCurrentChange(val) {
      Object.assign(this.params, {
        page: val
      });
      this.getThisData();
    },
    onSearch() {
      //来源单号查询
      if (this.timeValue) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      this.getThisData();
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        company_id: null,
        department_id: null,
        type: this.type
      });
      this.$set(this, "TableDataInfo", []);
      this.timeValue = [];
      this.timeValue2 = null;
      this.getThisData();
    },
    getThisData() {
      this.$axios({
        method: "get",
        url: this.Api,
        params: this.params
      }).then(res => {
        if (res.data.code == 200) {
          this.TableData.tableData = res.data.data.data;
          this.TableData.total = res.data.data.total;
        }
      });
    }
  },
  mounted() {
    switch (this.dialogParams.delivery_type) {
      case 1:
        this.order_snTitle = "关联三包出库单号";
        this.customer_nameTitle = "客户名称";
        this.Api = "Publics/OtherDelivery/quoteThreeOutstockList";
        this.type = 0;
        break;
      case 2:
        this.order_snTitle = "关联返胎入库单号";
        this.customer_nameTitle = "厂家名称";
        this.Api = "Publics/OtherDelivery/quoteRebornOutstockList";
        this.type = 3;
        break;
      case 3:
        this.order_snTitle = "关联旧胎退回单号";
        this.customer_nameTitle = "客户名称";
        this.Api = "Publics/OtherDelivery/quoteThreeOutstockList";
        this.type = 1;
        break;
    }
    this.$set(this, "params", {
      // company_id: this.dialogParams.company_id,
      // department_id: this.dialogParams.department_id,
      type: this.type
    });
    this.getThisData();
    this.$common.options("getWarehouseListOption", this.warehouseList, {
      company_id: this.params.company_id
    }); //仓库管理列表
  }
};
</script>
